<template>
  <div class="wallet">
    <el-form :inline="true">
      <el-row>
        <el-button
          class="wallet__btn--add"
          type="primary"
          size="mini"
          @click="submit()"
          >提现</el-button
        >
      </el-row>
    </el-form>
    <el-tabs v-model="search.order_status" type="card" @tab-click="searchWallet">
      <el-tab-pane label="全部" name="-1"></el-tab-pane>
      <el-tab-pane label="等待" name="0"></el-tab-pane>
      <el-tab-pane label="完成" name="1"></el-tab-pane>
      <el-tab-pane label="失败" name="2"></el-tab-pane>
    </el-tabs>
    <el-table :data="walletList" style="width: 100%" border stripe height="700">
      <el-table-column prop="address" label="地址"></el-table-column>
    </el-table>
  </div>
</template>
<script>
import { walletList } from "@/api/wallet.js";

export default {
  name: "Wallet",
  data() {
    return {
      search: {
        page: 1,
        limit: 15,
        status: -1,
        action: 0
      },
      walletList: [],
      total: 0
    };
  },
  methods: {
    searchWallet() {

    },
    getWalletList() {
      walletList(this.search).then(({ data: { data, total } }) => {
        this.walletList = data;
        this.total = total;
      })
    },
    submit() {

    }
  },
  created() {
    this.getWalletList();
  }
};
</script>

<style lang="scss">
$prefix: "wallet";

.#{$prefix} {
  &__btn--add {
    margin-bottom: 20px;
  }
}
</style>
